'use client'

import { useRef, useEffect } from 'react'
import Link from 'next/link'
import gsap from 'gsap'
import { Search, User, Heart, ShoppingBag } from 'lucide-react'
import { MainCategory } from '@/types/navigation'
import { useNavigation } from '@/hooks/useNavigation'
import { NavigationMenu } from './NavigationMenu'

interface NavigationBarProps {
  categories: MainCategory[]
}

export function NavigationBar({ categories }: NavigationBarProps) {
  const headerRef = useRef<HTMLElement>(null)
  const { state, toggleMenu, setActiveCategory, closeMenu } = useNavigation()

  useEffect(() => {
    const ctx = gsap.context(() => {
      gsap.from(headerRef.current, {
        yPercent: -100,
        opacity: 0,
        duration: 0.6,
        ease: 'power2.out',
      })
    })

    return () => ctx.revert()
  }, [])

  return (
    <header ref={headerRef} className="fixed top-0 left-0 right-0 z-50 bg-white">
      {/* Top Bar */}
      <div className="h-10 bg-black text-white flex items-center justify-between px-4">
        <div className="flex items-center space-x-2">
          <span className="text-sm">Ship To</span>
          <button className="text-sm underline">United Kingdom</button>
        </div>
        <p className="text-sm">New Lines Added | Enjoy up to 30% off *exclusions apply</p>
      </div>

      {/* Main Navigation */}
      <div className="h-20 border-b border-gray-200">
        <div className="container mx-auto h-full flex items-center justify-between px-4">
          {/* Logo */}
          <Link href="/" className="absolute left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2">
            <h1 className="text-2xl font-serif">ARIES</h1>
          </Link>

          {/* Navigation Items */}
          <nav className="flex items-center space-x-8">
            {categories.map((category) => (
              <button
                key={category._id}
                className="text-sm hover:text-gray-600 transition-colors"
                onMouseEnter={() => setActiveCategory(category._id)}
              >
                {category.title}
              </button>
            ))}
          </nav>

          {/* Icons */}
          <div className="flex items-center space-x-4">
            <button className="p-2 hover:bg-gray-100 rounded-full">
              <Search className="w-5 h-5" />
            </button>
            <button className="p-2 hover:bg-gray-100 rounded-full">
              <User className="w-5 h-5" />
            </button>
            <button className="p-2 hover:bg-gray-100 rounded-full">
              <Heart className="w-5 h-5" />
            </button>
            <button className="p-2 hover:bg-gray-100 rounded-full">
              <ShoppingBag className="w-5 h-5" />
            </button>
          </div>
        </div>
      </div>

      {/* Mega Menu */}
      <NavigationMenu
        categories={categories}
        isOpen={state.isOpen}
        activeCategory={state.activeCategory}
        onClose={closeMenu}
      />
    </header>
  )
}

